class Periodo {
  constructor(dados) {
    this.id = dados ? dados.id : null;
    this.nome = dados ? dados.nome : null;
    this.sigla = dados ? dados.sigla : null;
    this.numero = dados ? dados.numero : null;
    this.idCurso = dados ? dados.idCurso : null;
    this.idTipoPeriodo = dados ? dados.idTipoPeriodo : null;
    this.nomeTipoPeriodo = dados ? dados.nomeTipoPeriodo : null;
    this.idFaixaAno = dados ? dados.idFaixaAno : null;
    this.nomeFaixaAno = dados ? dados.nomeFaixaAno : null;
    this.valor = dados ? dados.valor : 0.0;
    this.idNivel = null;
  }

  static validations(required, maxLength, numeric, decimal) {
    return {
      nome: { required, maxLength: maxLength(30) },
      sigla: { required, maxLength: maxLength(10) },
      numero: { required, numeric },
      idCurso: { required },
      idTipoPeriodo: { required },
      idFaixaAno: { required },
      idNivel: { required },
      valor: { required, decimal }
    };
  }

  static validationMessages() {
    return {
      nome: {
        required: "Nome  obrigatrio",
        maxLength: "Nome s aceita no mximo 30 caracteres"
      },
      sigla: {
        required: "Sigla  obrigatria",
        maxLength: "Sigla s aceita no mximo 10 caracteres"
      },
      numero: {
        required: "Nmero  obrigatrio",
        numeric: "Somente nmero"
      },
      valor: {
        required: "Valor  obrigatrio",
        decimal: "Somente nmero decimal"
      },
      idNivel: {
        required: "Nvel  obrigatrio"
      },
      idCurso: {
        required: "Curso  obrigatrio"
      },
      idTipoPeriodo: {
        required: "Tipo de Perodo  obrigatrio"
      },
      idFaixaAno: {
        required: "Faixa Ano  obrigatrio"
      }
    };
  }
}
export default Periodo;
