import baseService from "./BaseService";
import mixinsMessage from "@/mixins/message";
export default {
  logar(login) {
    return baseService
      .post("/api/auth", login)
      .then(response => {
        return response.data;
      })
      .catch(error => {
        mixinsMessage.methods.error("Usurio ou Senha invlidos");
        // eslint-disable-next-line no-console
        console.error(error);
      });
  },
  logout(login) {
    return baseService
      .post("/api/auth/logout", login)
      .then(response => {
        return response.data;
      })
      .catch(error => {
        mixinsMessage.methods.error("No foi possvel fazer logout");
        // eslint-disable-next-line no-console
        console.error(error);
      });
  }
};
